.data-table {
	width: 100%;

	table {
		position: relative;
		width: 100%;
		table-layout: fixed;
		border-spacing: 0;

		&::before {
			content: '';
			display: block;
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 1px;
			background-color: var(--color-base-000);
		}

		thead {
			th {
				padding: 16px;
				border-bottom: 0;
				// border-bottom: 1px solid #dfdfdf;
				background-color: #f7f7f7;
				font-size: 14px;
			}
		}

		tbody {
			th,
			td {
				padding: 12px 20px;
				border-width: 1px;
				border-style: solid;
				border-color: #dedede;
				// border-bottom: 1px solid #ebebeb;
				font-size: 14px;
				text-align: center;

				&:first-child {
					border-left: none;
				}

				&:last-child {
					border-right: none;
				}
			}

			th {
				font-weight: 600;
			}

			td {
				&.content {
					text-align: left;

					.subject {
						display: flex;
						align-items: center;

						&:hover {
							text-decoration: underline;
						}

						.badge {
							flex-shrink: 0;
							margin-right: 4px;
						}

						.ico {
							flex-shrink: 0;
							display: inline-flex;
							align-items: center;
							justify-content: center;
						}

						.link {
							@include ellipsis(1);
						}
					}

					p.subject {
						line-height: 20px;
					}
					
					p.summary {
						line-height: 20px;
					}

					.ellipsis-01 {
						display: block;
						@include ellipsis(1);
					}
				}

				&.unanswer {
					color: var(--color-base-060);
				}
	
				&.answer {
					color: var(--color-primary-040);
				}
			}

			.align {
				&-lt {
					text-align: left;
				}

				&-rt {
					text-align: right;
				}

				&-ct {
					text-align: center;
				}
			}

			.nodata {
				padding: 60px 0;

				p {
					line-height: 24px;
				}
			}

			.badge {
				&.new {
					flex-shrink: 0;
					display: inline-flex;
					justify-content: center;
					align-items: center;
					width: 22px;
					height: 22px;
					border-radius: 50%;
					background-color: var(--color-primary-040);
					font-size: 14px;
					font-weight: 700;
					color: var(--color-base-100);
				}
			}

			.ico {
				&::before {
					content: "";
					display: inline-block;
					width: var(--ico-size);
					height: var(--ico-size);
					mask-repeat: no-repeat;
					mask-position: 50%;
					mask-size: cover;
					mask-image: var(--ico-image);
					background-color: var(--color-base-010);
				}

				&.file {
					&::before {
						--ico-size: 24px;
						mask-image: icon(attatchment);
					}

					.ico-text {
						@include hidden;
					}
				}
			}
		}
	}
}