.btn {
	&-a {
	// 기본 설정 
	// btn-a 사이즈 동일, style-a 스타일 분리(컬러, 보더 등)
	// btn-a default 가장 많이 사용하는 버튼
		--height: 52px;
		--padding: 0 26px;
		--radius: 4px;
		--fontSize: 16px;
		--fontWeight: 500;
		--lineHeight: 24px;
		--iconBg: var(--color-base-100);

		display: inline-flex;
		align-items: center;
		justify-content: center;
		width: auto;
		height: var(--height);
		padding: var(--padding);
		box-sizing: border-box;
		// box-shadow: inset 0px 0px 0 1px var(--borderColor);
		border-radius: var(--radius);
		background-color: var(--bgColor);
		font-size: var(--fontSize);
		font-weight: var(--fontWeight);
		line-height: var(--lineHeight);
		color: var(--color);
		text-align: center;

		&:not(.disabled):not(:disabled) {
			--bgColor: var(--color-base-020);
			--color: var(--color-base-100);
		}

		&:hover {
			box-shadow: 2px 2px 6px 2px rgba($color: #ababab, $alpha: 0.2);
		}

		// &[disabled],
		// &.disabled {
		// 	--iconBg: var(--color-base-010)
		// }

		// style-a
		&.style-a:not(.disabled):not(:disabled) {
			--bgColor: var(--color-primary-040);
			--color: var(--color-base-100);
			--iconBg: var(--color-base-100);
		
			&.type-line:not(.disabled):not(:disabled) {
				--borderColor: var(--color-primary-040);
				--color: var(--color-primary-040);
			}
		}

		// style-b
		&.style-b:not(.disabled):not(:disabled) {
			--bgColor: var(--color-secondary-040);
			--color: var(--color-base-100);
			--iconBg: var(--color-base-100);
		
			&.type-line:not(.disabled):not(:disabled) {
				--borderColor: var(--color-secondary-040);
				--color: var(--color-secondary-040);
			}
		}

		// style-c
		&.style-c:not(.disabled):not(:disabled) {
			--borderColor: var(--color-base-070);
			--bgColor: var(--color-base-100);
			--color: var(--color-base-010);
			--iconBg: var(--color);

			border: 1px solid var(--borderColor);
		}

		&.type-fill:not(.disabled):not(:disabled) {
			--bgColor: var(--color-base-070);
		}

		// icon button styles
		&[class*="ico-"] {
			--ico-width: 16px;
			--ico-height: 16px;

			gap: 4px;

			&:not([class*=-rtl]) {
				&::before {
					content: '';
					display: block;
					width: var(--ico-width);
					height: var(--ico-height);
					mask-repeat: no-repeat;
					mask-position: 50%;
					mask-size: cover;
					background-color: var(--iconBg);
				}
			}
			

			&[class*=-rtl] {
				&::after {
					content: '';
					display: block;
					width: var(--ico-width);
					height: var(--ico-height);
					mask-repeat: no-repeat;
					mask-position: 50%;
					mask-size: cover;
					background-color: var(--iconBg);
				}
			}		
		}

		// &[class*=ico-pin] { &::before, &::after { mask-image: icon(g-pin, --color-base-100); } }

		// size
		$btn-style-size: (
			// name size font-size padding
			'small' 36px 14px 18px,
			'medium' 52px 16px 26px,
			'large' 64px 20px 32px,
		);
		
		@each $name, $size, $fontSize, $padding in $btn-style-size {
			&.#{$name} {
				// --min-width: #{$outline};
				--height: #{$size};
				--lineHeight: #{$size};
				--fontSize: #{$fontSize};
				--padding: 0 #{$padding};
	
				&[class*="ico-"] {
					--padding: 0 calc(#{$padding} - 4px) 0 #{$padding};

					&::before,
					&::after {
						width: #{$fontSize};
						height: #{$fontSize};
					}
				}
			}
		}

		// text button styles
		&.btn-em {
			padding: 0;
			background-color: inherit;
			border: 0;
			color: inherit;

			&.underline {
				text-decoration: underline;
			}

			&.link {
				height: auto;
				line-height: 1;

				&:hover {
					text-decoration: underline;
				}
			}

			&[class*="ico-"] {
				--iconBg: var(--color-base-000);
				// 텍스트 컬러랑 같아야함

				gap: 2px;
			}

			&:hover {
				box-shadow: none;
			}
		}
	}
	
	
}

.btn-a {
	&:focus {
		outline-offset: -2px;
		outline: 2px solid rgba(36, 107, 235, 0.8);
	}
}

:where([class*=btn-]){
	// button disabled styles
	&[class*="disabled"],
	&[disabled] {
		--bgColor: var(--color-base-080);
		--color: var(--color-base-060);
		--iconBg: var(--color-base-060);

		background-color: var(--bgColor);
		color: var(--color);
		box-shadow: none;

		&:hover {
			box-shadow: none;
		}
	}

	// &:focus {
	// 	outline-offset: -2px;
	// 	outline: 2px solid rgba(36, 107, 235, 0.8);
	// }

	&.flex {
		flex: 1 1 100%;
	}

	&[class*=ico-eye-open] { &::before, &::after { mask-image: icon(eye-open, --color-base-100); } }
	&[class*=ico-share] { &::before, &::after { mask-image: icon(share, --color-base-100); } }
	&[class*=ico-heart] {
		&::before, &::after { mask-image: icon(heart, --color-base-100); }
		&.active {&::before, &::after { mask-image: icon(heart-fill, --color-base-100); }}
	}
	&[class*=ico-heart-fill] { &::before, &::after { mask-image: icon(heart-fill, --color-base-100); } }
}

.button-display {
	display: flex;
	justify-content: center;

	&.align-lt {
		justify-content: flex-start;
	}

	&.align-rt {
		justify-content: flex-end;
	}

	&.align-ct {
		justify-content: center;
	}

	.button-area {
		flex: 1 1 100%;
		justify-content: center;
		// width: 100%;
	}
}

.button-area {
	display: flex;

	&.align-lt {
		justify-content: flex-start;
	}

	&.align-rt {
		justify-content: flex-end;
	}

	&.align-ct {
		justify-content: center;
	}
}
