.tab-display {
	&.style-a {
		width: 100%;

		.tab-list {
			display: flex;

			.tab-item {
				flex: 1 ;

				&.current {
					.tab-text {
						background-color: var(--color-base-090);
						color: var(--color-primary-040);

						&::after {
							content: "";
							display: block;
							position: absolute;
							top: auto;
							left: 0;
							bottom: 0;
							width: 100%;
							height: 2px;
							background-color: var(--color-primary-040);
						}
					}
				}

				.tab-text {
					display: block;
					position: relative;
					padding: 16px 0;
					box-shadow: inset 0px -1px 0px var(--color-base-080);
					background-color: var(--color-base-100);
					font-size: 16px;
					font-weight: 600;
					line-height: 30px;
					color: var(--color-base-010);
					text-align: center;
					cursor: pointer;
				}
			}
		}
	}

	&.style-b {
		.tab-list {
			overflow: hidden;
			display: flex;
			gap: 12px;
			padding: 8px;
			border-radius: 16px;
			background-color: var(--color-base-090);

			.tab-item {
				border-radius: 16px;
				background-color: var(--color-base-095);

				&.current {
					background-color: var(--color-primary-040);

					.tab-text {
						font-weight: 500;
						color: var(--color-base-100);
					}
				}

				.tab-text {
					display: block;
					position: relative;
					padding: 8px 12px;
					// box-shadow: inset 0px -1px 0px color(base, 060);
					// background-color: var(--color-base-100);
					font-size: 16px;
					line-height: 24px;
					color: var(--color-base-010);
					text-align: center;
					cursor: pointer;
				}
			}
		}
	}
}

.tab-contents {
	.tab-content {
		visibility: visible;
		opacity: 1;
		transition: opacity 0.3s ease, visibility 0.3s ease;

		&:not(.on) {
			display: none;
			visibility: hidden;
			opacity: 0;
		}
	}
}