/********************************************************************************************************************************

 * dialog-popup

********************************************************************************************************************************/
.dialog-popup {
	display: flex;
	justify-content: center;
	align-items: flex-start;
	overflow: hidden;
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: -1;
	width: 100%;
	height: 100%;
	padding: 20px;
	box-sizing: border-box;
	background-color: rgba(0, 0, 0, 0.4);
	transition: visibility 0.15s 0.3s, z-index 0s 0.3s, opacity 0.2s;
	opacity: 0;

	&.active {
		z-index: 100;
		transition-delay: 0s;
		opacity: 1;
		visibility: visible;
	}

	.popup-wrap {
		flex: 1 1 100%;
		align-self: center;
		overflow: hidden;
		display: flex;
		flex-direction: column;
		max-width: 400px;
		max-height: 100%;
		border-radius: 8px;
		background-color: var(--color-base-100);
		box-sizing: border-box;

		.popup-header {
			display: flex;
			justify-content: space-between;
			padding: 8px 8px 0;
	
			.btn-popup-close {
				flex-shrink: 0;
				overflow: hidden;
				position:relative;
				z-index: 20;
				width: 32px;
				height: 32px;
				margin-left: auto;
				text-indent: -999px;

				&::before {
					content: '';
					display: block;
					width: 32px;
					height: 32px;
					mask: icon(close) no-repeat 50%/cover;
					background-color: var(--color-base-010);
				}
			}
		}
	
		.popup-body {
			padding: 0 0 32px;
			@include overflow-scroll(default, dark, medium, y);
	
			.popup-contents {
				padding: 0 40px;
	
				>.para {
					font-size: 18px;
					line-height: 28px;
					text-align: center;

					.wbr {
						display: block;
						word-break: keep-all;
					}
				}

				.align-ct {
					text-align: center;
				}
			}
		}
	
		.popup-footer {
			border-top: 1px solid var(--color-base-080);
	
			.button-area {
				display: flex;
				column-gap: 0;
	
				[class*=btn-] {
					display: inline-flex;
					justify-content: center;
					align-items: center;
					flex: 1 1 100%;
					height: 52px;
					background-color: var(--color-base-100);
					font-weight: 500;
					line-height: 24px;
					color: var(--color-base-010);
	
					&:last-child {
						background-color: var(--color-base-010);
						color: var(--color-base-100);
					}

					&.disabled {
						background-color: var(--color-base-080);
						color: var(--color-base-060);
					}
				}
			}
		}
	}

	@include respond-below(tablet) {
		.popup-wrap {
			min-width: 100%;

			.popup-body {
				.popup-contents {
					padding: 0 24px;

					>.para {
						max-width: 280px;
						margin: 0 auto;
						font-size: 16px;
						line-height: 24px;
						word-break: keep-all;
					}
				}
			}
		}
	}
}

/********************************************************************************************************************************

 * content-popup

********************************************************************************************************************************/
.content-popup {
	display: flex;
	justify-content: center;
	align-items: flex-start;
	overflow: hidden;
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: -1;
	width: 100%;
	height: 100%;
	padding: 20px;
	box-sizing: border-box;
	background-color: rgba(0, 0, 0, 0.4);
	transition: visibility 0.15s 0.3s, z-index 0s 0.3s, opacity 0.2s;
	opacity: 0;

	&.small {
		.popup-wrap {
			max-width: 480px;
		}
	}


	&.active {
		z-index: 100;
		transition-delay: 0s;
		opacity: 1;
		visibility: visible;
	}

	.popup-wrap {
		flex: 1 1 100%;
		align-self: center;
		overflow: hidden;
		display: flex;
		flex-direction: column;
		max-width: 600px;
		max-height: 100%;
		box-sizing: border-box;
		background-color: var(--color-base-100);

		.popup-header {
			display: flex;
			justify-content: center;
			position: relative;
			margin-bottom: 32px;
			padding: 40px 70px 0;
	
			.popup-subject {
				font-size: 22px;
				font-weight: 600;
				line-height: 32px;
				color: var(--color-base-010);
				text-align: center;
			}
	
			.btn-popup-close {
				flex-shrink: 0;
				overflow: hidden;
				position: absolute;
				top: 40px;
				right: 32px;
				left: auto;
				z-index: 20;
				width: 30px;
				height: 30px;
				text-indent: -999px;

				&::before {
					content: '';
					display: block;
					width: 30px;
					height: 30px;
					mask: icon(close) no-repeat 50%/cover;
					background-color: var(--color-base-010);
				}
			}
		}
	
		.popup-body {
			padding: 0 0 32px;
			@include overflow-scroll(default, dark, medium, y);
	
			.popup-contents {
				padding: 0 32px;
			}
		}
	
		.popup-footer {
			border-top: 1px solid var(--color-base-080);
			
			.button-area {
				display: flex;
				justify-content: center;
	
				[class*=btn-] {
					flex: 1 1 100%;
					display: inline-flex;
					justify-content: center;
					align-items: center;
					position: relative;
					height: 52px;
					background-color: var(--color-base-100);
					font-weight: 500;
					line-height: 24px;
					color: var(--color-base-010);

					&::before {
						content: '';
						display: block;
						position: absolute;
						top: 0;
						left: 0;
						width: 1px;
						height: 100%;
						background-color: var(--color-base-080);
					}

					&:last-child {
						background-color: var(--color-base-010);
						color: var(--color-base-100);
					}

					&.disabled {
						background-color: var(--color-base-080);
						color: var(--color-base-060);
					}
				}
			}
		}
	}

	// &.small {
	// 	.popup-wrap {
	// 		max-width: 480px;
	// 	}
	// }

	@include respond-below(tablet) {
		.popup-wrap {
			max-width: 100% !important;
			padding-bottom: 0px;

			.popup-header {
				padding:24px 52px 0;

				.popup-subject {
					font-size: 20px;
					line-height: 30px;

					br {
						display: none;
					}
				}

				.btn-popup-close {
					top: 25px;
					right: 16px;
					width: 28px;
					height: 28px;
				}
			}

			.popup-body {
				.modal-contents {
					padding: 0 24px;
				}
			}

			.popup-footer {		
				.button-area {
					column-gap: 0;
	
					.btn {
						max-width: 100%;
					}
				}
			}
		}
	}

	@include respond-below(mobile) {
		padding: 0;

		.popup-wrap {
			align-self: stretch;
			
			.popup-body {
				flex: 1 1 100%;
			}
		}
	}

	// module styles
	.popup-contents {
		.terms-display {
			row-gap: 32px;
		
			.terms-title {
				font-size: 20px;
				line-height: 30px;
		
				@include respond-below(tablet) {
					font-size: 18px;
					line-height: 28px;
				}
	
				.wbr {
					display: block;
	
					@include respond-below(tablet) {
						display: inline;
					}
				}
			}
		
			.terms-content {
				>div.order-list,
				>ol.order-list {
					&:only-child {
						>.order-item {
							margin: 0;
						}
					}
	
					>.order-item {
						margin: 32px 0;
					}
				}
			}
		
			.title {
				margin-bottom: 32px;
				font-size: 22px;
				line-height: 32px;
		
				@include respond-below(tablet) {
					font-size: 22px;
					line-height: 32px;
				}
	
				&:not(:first-child) {
					margin-top: 32px;
					padding-top: 32px;
				}
			}
			
			.desc-list {
				.desc-item {
					font-size: 16px;
					line-height: 24px;
					color: var(--color-base-020);
	
					+.desc-item {
						margin-top: 24px;
					}
		
					>.desc-list {
						margin-top: 8px;
	
						.desc-item {
							margin: 4px 0;
						}
					}
				}
			}
		
			.unit-important {
				display: flex;
				gap: 4px;
				padding-left: 4px;
				color: var(--color-base-030);
		
				&::before {
					content: '※';
					flex-shrink: 0;
					color:var(--color-base-030);
				}
			}
		
	
			ol.order-list {
				padding: 0;
				list-style: none;
				list-style-type: none;
				counter-reset: order-count;
		
				>.order-item {
					list-style: none;
					list-style-type: none;
					counter-increment: order-count;
		
					>.subject {
						&::before {
							content: counter(order-count) ". ";
						}
					}
				}
			}
		
			.order-list {
				>.order-item {
					.subject {
						font-size: 20px;
						line-height: 30px;
						
						@include respond-below(tablet) {
							font-size: 20px;
							line-height: 30px;
						}
					}
	
					ol.order-list {
						counter-reset: order-count;
		
						.order-item {
							display: flex;
							column-gap: 4px;
							font-size: 16px;
							line-height: 24px;
							color: var(--color-base-030);
							counter-increment: order-count;
		
							+.order-item {
								margin-top: 4px;
							}
		
							&::before {
								content: counter(order-count) ". ";
								flex-shrink: 0;
							}
						}
					}
				}
			}
		
			.table-area {
				margin: 8px 0;
				
				table {
		
					thead {
						tr {
							th {
								padding: 10px 8px;
		
								@include respond-below(tablet) {
									padding: 10px 8px;
									font-size: 14px;
									line-height: 20px;
								}
							}
						}
					}
		
					tbody {
						tr {
							td {
								padding: 12px;
								vertical-align: top;
		
								@include respond-below(tablet) {
									padding: 10px;
									font-size: 14px;
									line-height: 20px;
								}
							}
						}
					}
				}
			}
		}
	}

	// 뉴스레터
	.agreement-display {
		.agreement-wrap {
			row-gap: 16px;
			padding: 20px 0;
		}
	}
}

/********************************************************************************************************************************

 * toast-popup

********************************************************************************************************************************/
.toast-popup {
	display: flex;
	justify-content: center;
	align-items: flex-start;
	overflow: hidden;
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: -1;
	width: 100%;
	height: 100%;
	padding: 40px 20px;
	box-sizing: border-box;
	transition: visibility 0.15s 0.3s, z-index 0s 0.3s, opacity 0.2s;
	opacity: 0;

	&.active {
		z-index: 100;
		transition-delay: 0s;
		opacity: 1;
		visibility: visible;

		.popup-wrap {
			transform: translateY(0);
		}
	}

	.popup-wrap {
		flex: 1 1 100%;
		align-self: flex-end;
		overflow: hidden;
		display: flex;
		flex-direction: column;
		max-width: 320px;
		max-height: 100%;
		box-sizing: border-box;
		border-radius: 4px;
		background-color: rgba(0, 0, 0, 0.90);
		transform: translateY(100%);
		transition: transform 0.3s ease-in-out;

		.popup-body {
			.popup-contents {
				padding: 12px 24px;

				.para {
					font-size: 16px;
					font-weight: 500;
					line-height: 24px;
					color: var(--color-base-100);
					text-align: center;
				}
			}
		}
	}

	// &.small {
	// 	.popup-wrap {
	// 		max-width: 320px;
	// 	}
	// }

	@include respond-below(tablet) {
		padding: 32px 20px;

		.popup-wrap {
			max-width: 100% !important;
		}
	}
}

