	// 갤러리 상세 슬라이드
	.slides-veiw-area {
		position: relative;
		padding: 0 104px;
		box-sizing: border-box;

		.history-view-slides {
			overflow: hidden;
			
			.slide-list {
				.slide-item {
					// width: 1152px;
					// height: 600px;

					// >a { display: block; }

					.image {
						display: block;
						width: 100%;
						height: 100%;
						text-align: center;
					}

					img {
					}
				}
			}

			.swiper-pagination {
				position: absolute;
				top: auto;
				bottom: calc(100% + 46px);
				font-size: 20px;
				font-weight: 600;
				line-height: 30px;
				color: var(--color-base-100);
			}

			[class*="swiper-button"] {
				position: absolute;
				top: calc(50% - 32px);
				z-index: 1;

				&::before {
					content: '';
					display: block;
					width: 64px;
					height: 64px;
					mask-repeat: no-repeat;
					mask-size: cover;
					mask-position: 50%;
					background-color: var(--color-base-100);
					opacity: 0.5;
					cursor: pointer;
				}

				.btn-text {
					@include hidden;
				}

				// &.swiper-button-disabled {
				// 	display: none;
				// }

				&.swiper-button-next {
					right: 0;
					&::before { mask-image: icon(arrow-right); }
				}
		
				&.swiper-button-prev {
					left: 0;
					&::before { mask-image: icon(arrow-left); }
				}
			}
		}

		@include respond-below(tablet) {
			height: 100%;
			padding: 0;

			.history-view-slides {
				height: 100%;

				.slide-list {
					.slide-item {
						width: 100%;
						height: auto;

						.image {
							display: flex;
							align-items: center;
							justify-content: center;
						}

						img {
							// width: 100%;
							height: auto;
						}
					}
				}

				.swiper-pagination {
					position: absolute;
					top: auto;
					bottom: 100%;
					font-size: 16px;
					line-height: 24px;
				}

				[class*="swiper-button"] {
					position: absolute;
					top: calc(50% - 20px);
					width: 40px;
					height: 40px;
					background-color: rgba(0, 0, 0, 0.20);

					&::before {
						width: 40px;
						height: 40px;
						opacity: 1;
					}
					&.swiper-button-next {
						right: 0;
						&::before { mask-image: icon(arrow-right); }
					}
			
					&.swiper-button-prev {
						left: 0;
		
						&::before { mask-image: icon(arrow-left); }
					}
				}
