/********************************************************************************************************************************

 * typography

********************************************************************************************************************************/
// 페이지 단위 타이틀
.heading-l {
	font-size: 56px;
	font-weight: 700;
	line-height: 150%;
}

.heading-m {
	font-size: 48px;
	font-weight: 700;
	line-height: 150%;
}

.heading-s {
	font-size: 32px;
	font-weight: 700;
	line-height: 150%;
}

// 템플릿, 모듈 단위 텍스트
.title-l {
	font-size: 32px;
	font-weight: 700;
	line-height: 150%;
}

.title-m {
	font-size: 28px;
	font-weight: 500;
	line-height: 150%;
}

.title-s {
	font-size: 24px;
	font-weight: 500;
	line-height: 150%;
}

// body 본문 텍스트
.body-l {
	font-size: 24px;
	font-weight: 400;
	line-height: 150%;
}

.body-m {
	font-size: 16px;
	font-weight: 400;
	line-height: 150%;
}

.body-s {
	font-size: 14px;
	font-weight: 400;
	line-height: 150%;
}

@include respond-below(tablet) {
	.heading-l {
		font-size: 40px;
	}
	
	.heading-m {
		font-size: 36px;
	}
	
	.heading-s {
		font-size: 32px;
		font-weight: 700;
		line-height: 150%;
	}
	
	// 템플릿, 모듈 단위 텍스트
	.title-l {
		font-size: 24px;
	}
	
	.title-m {
		font-size: 20px;
	}
	
	.title-s {
		font-size: 18px;
	}
	
	// body 본문 텍스트
	.body-l {
		font-size: 18px;
	}
	
	.body-m {
		font-size: 14px;
	}
	
	.body-s {
		font-size: 12px;
	}
}

/********************************************************************************************************************************

 * data-finder

********************************************************************************************************************************/
.data-finder {
	display: flex;
	align-items: flex-end;
	gap: 16px;
	justify-content: space-between;

	.data-search {
		display: flex;
		gap: 8px;

		.search-type {
			.form-search {
				min-width: 100px;
				height: 52px;
				padding: 0 20px 0 10px;
				border: 1px solid var(--color-base-080);
				background: icon(chevron-down) no-repeat right 10px center / 20px;
				font-size: 16px;
				vertical-align: middle;
			}
		}
	
		.search-word {
			position: relative;
			
			.search-form {
				display: flex;
				column-gap: 15px;
				height: 52px;
				padding: 12px 0;
				border-bottom: 2px solid var(--color-base-010);
				box-sizing: border-box;

				.form {
					.form-elem {
						width: 100%;
						height: 100%;
						padding-right: 24px;
						border: 0;
						box-sizing: border-box;
						font-size: 16px;
						vertical-align: middle;
		
						&::placeholder {
							color: var(--color-base-050);
						}
					}
	
					.form-btn {
						position: absolute;
						top: calc(50% - 24px/2);
						right: 0;
						display: flex;
						column-gap: 4px;
					}
				}
			}

			.btn-cancel {
				display: block;

				&::before {
					content: '';
					display: block;
					width: 24px;
					height: 24px;
					mask: icon(delete-circle) no-repeat 50%/cover;
					background-color: var(--color-base-010);
				}
			}

			.btn-search {
				display: inline-flex;
				align-items: center;
				justify-content: center;
				width: 24px;
				height: 24px;
				padding: 0;

				&::before {
					content: '';
					display: block;
					width: 100%;
					height: 100%;
					mask: icon(search) no-repeat center center;
					background-color: var(--color-base-010);
				}

				.btn-name {
					@include hidden;
				}
			}
		}
	}

	@include respond-below(tablet) {
		align-items: center;
		// flex-direction: column;
	}
}

/********************************************************************************************************************************

 * pagination

********************************************************************************************************************************/
.pagination {
	.page-wrap {
		display: flex;
		justify-content: center;
		align-items: center;
	}

	.page-btn {
		flex-shrink: 0;
		display: block;
		width: 40px;
		height: 40px;
		border: 1px solid var(--color-base-080);
		box-sizing: border-box;

		+.page-btn {
			margin-left: 8px;
		}

		&::before {
			content: '';
			display: block;
			width: 100%;
			height: 100%;
			mask-size: 28px;
			mask-repeat: no-repeat;
			mask-position: center;
			background-color: var(--color-base-000);
		}

		&.page-first::before { mask-image: icon(chevron-left-last); }
		&.page-prev::before { mask-image: icon(chevron-left); }
		&.page-next::before { mask-image: icon(chevron-right); }
		&.page-last::before { mask-image: icon(chevron-right-last); }
	}

	.page-num {
		display: flex;
		column-gap: 16px;
		margin: 0 24px;
		transition: all .3s ease-in-out;

		.page-link  {
			display: inline-flex;
			justify-content: center;
			align-items: center;
			position: relative;
			width: 40px;
			height: 40px;
			font-size: 18px;
			font-weight: 600;
			line-height: 28px;
			color: var(--color-base-070);
			transition: all .3s;

			&:hover {
				color: var(--color-base-000);
			}

			&.current {
				color: var(--color-primary-040);

				&::after {
					content: '';
					display: block;
					position: absolute;
					bottom: 6px;
					left: auto;
					width: 24px;
					height: 2px;
					background-color: var(--color-primary-040);
				}
			}
		}
	}

	@include respond-below(tablet) {
		.page-btn {
			width: 32px;
			height: 32px;

			&::before {
				mask-size: 24px;
			}

			+.page-btn {
				margin-left: 6px;
			}
		}

		.page-num {
			column-gap: 12px;
			margin: 0 14px;

			.page-link {
				width: 32px;
				height: 32px;
				font-size: 16px;
				line-height: 24px;

				&:nth-child(n+4) {
					display: none;
				}

				&.current {
					&::after {
						bottom: 2px;
						width: 16px;
					}
				}
			}
		}
	}

	@include respond-below(mobile-sm) {
		.page-btn {
			width: 24px;
			height: 24px;

			&::before {
				mask-size: 20px;
			}

			+.page-btn {
				margin-left: 6px;
			}
		}

		.page-num {
			margin: 0 auto;

			.page-link {
				width: 24px;
				height: 24px;
				font-size: 14px;
				line-height: 20px;
			}
		}
	}
}